














































































































import {
    Component,
    Vue,
    Watch,
} from 'vue-property-decorator';
import {
    Core
} from '@/store/core'
import {
    Auth
} from '@/store/auth'
import {
    User
} from '@/store/user'

import {
    City
} from "@/store/city";
import moment from "moment";
import {App} from "@/store/app";

@Component({
    components: {},
    computed: {}
})

export default class Home extends Vue {
    private formUser: any = {}
    private formProfile: any = {}
    private prefix: any[] = []
    private gender: any[] = []
    private group: any[] = []
    async created() {
        await this.run()
    }

    async run() {
        this.prefix = await Core.getChoice('คำนำหน้า')
        this.gender = await Core.getChoice('เพศ')
        this.group = await Core.getChoice('กลุ่มเกษตรกร')
    }

    @Watch('formProfile.birthday')
    async onChangeProvince(val: string) {
        this.formProfile.age = moment().diff(val, 'years', false);

    }

    async register() {

        let user = await Auth.registerAdmin(this.formUser)
        if (user.id) {
            await App.success("เพิ่มผู้ดูแลระบบสำเร็จ")
            this.formUser = {}
        } else {
            await App.error("เกิดข้อผิดพลาดในการเพิ่มข้อมูลผู้ดูแลระบบ")
        }
    }

    async openCityDialog() {
        City.dialogCityState = true
    }

    get CityFrom() {
        return City.showName
    }

}
